@import url("https://fonts.googleapis.com/css2?family=Barlow+Condensed:wght@200;300;400;500;600;700&family=Epilogue:wght@500;700&display=swap");
body {
  font-family: "Epilogue";
  margin: 0;
}

h2 {
  font-weight: 700;
  font-size: 35px;
  color: hsl(0deg, 0%, 8%);
  margin: 0;
}

p {
  font-size: 16px;
  color: hsl(0deg, 0%, 41%);
  line-height: 1.8rem;
  margin: 0;
}

header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.7rem;
}
header .open-sidebar {
  cursor: pointer;
}

@media (min-width: 750px) {
  header {
    padding: 0 1rem;
    justify-content: center;
  }
  header h2 {
    margin-left: 1rem;
  }
  header .open-sidebar {
    display: none;
  }
}
main {
  display: flex;
  flex-direction: column;
  height: 100%;
}
main .hero-image {
  background-image: url("images/image-hero-mobile.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  width: 100%;
  aspect-ratio: 1.33/1;
  margin: 1.5rem auto;
}
main section {
  display: flex;
  flex-direction: column;
  text-align: center;
  padding: 0.7rem;
}
main section .learn-more {
  cursor: pointer;
  margin: 0.2rem auto 3rem;
  border: 2px solid hsl(0deg, 0%, 8%);
  border-radius: 0.8rem;
  background-color: hsl(0deg, 0%, 8%);
  padding: 0.6rem 1rem;
}
main section .learn-more:hover {
  background-color: hsl(0deg, 0%, 98%);
}
main section .learn-more:hover p {
  color: hsl(0deg, 0%, 8%);
}
main section .learn-more p {
  color: hsl(0deg, 0%, 98%);
  font-weight: 700;
}
main section p {
  margin: 8px auto;
}
main section .client-logos {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
main section .client-logos img {
  width: 20%;
}

@media (max-width: 749px) {
  .navigation-container {
    pointer-events: none;
    position: absolute;
    inset: 0;
    z-index: 999;
    background-color: rgba(0, 0, 0, 0);
    transition: background-color 0.3s ease-in-out;
    overflow: hidden;
  }
  .navigation-container .navigation {
    pointer-events: all;
    position: absolute;
    inset: 0 0 0 35%;
    transform: translateX(100%);
    transition: transform 0.3s ease-in-out;
    background: white;
  }
  .navigation-container .navigation .close-sidebar {
    position: absolute;
    inset: 1.2rem 1.2rem auto auto;
    cursor: pointer;
  }
  .navigation-container .navigation .nav-item-container {
    padding: 4.2rem 0 0 1.5rem;
    flex-direction: column;
    gap: 1rem;
  }
  .show-sidebar {
    background-color: rgba(0, 0, 0, 0.7);
    pointer-events: all;
  }
  .show-sidebar .navigation {
    transform: translateX(0);
  }
}
.navigation-container .navigation .nav-item-container {
  display: flex;
}
.navigation-container .navigation .nav-item-container li {
  display: flex;
  align-items: center;
  list-style: none;
}
.navigation-container .navigation .nav-item-container li .dropdown-toggle {
  cursor: pointer;
  margin-left: 1rem;
}
.navigation-container .navigation .registration-buttons-container {
  display: flex;
  flex-direction: column;
  font-family: inherit;
  gap: 0.2rem;
}
.navigation-container .navigation .registration-buttons-container .registration-button {
  padding: 0.3rem 0;
  background-color: transparent;
  margin-inline: 1.5rem;
  font-family: inherit;
  border-radius: 0.9rem;
  border: 2px solid hsl(0deg, 0%, 41%);
  cursor: pointer;
}
.navigation-container .navigation .registration-buttons-container .registration-button:hover {
  border-color: hsl(0deg, 0%, 8%);
}
.navigation-container .navigation .registration-buttons-container .registration-button:hover p {
  color: hsl(0deg, 0%, 8%);
}
.navigation-container .navigation .registration-buttons-container .login-button {
  border: none;
}

@media (min-width: 750px) {
  .navigation-container {
    width: 100%;
    position: block;
  }
  .navigation-container .navigation {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .navigation-container .navigation .close-sidebar {
    display: none;
  }
  .navigation-container .navigation .nav-item-container {
    gap: 2rem;
  }
  .navigation-container .navigation .nav-item-container li .dropdown-toggle {
    margin-left: 0.5rem;
  }
  .navigation-container .navigation .registration-buttons-container {
    flex-direction: row;
    gap: 0;
  }
  .navigation-container .navigation .registration-buttons-container .registration-button {
    padding-inline: 1.5rem;
    margin-inline: 1rem;
  }
}

